.homepage-container {
    width: 100vw;
    min-height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
    background-color: rgb(255, 255, 255);
    overflow-y: auto; /* גלילה אנכית בלבד */
    overflow-x: hidden; /* מניעת גלילה אופקית */
    padding: 20px;
    padding-top: 12vh; /* ריווח מספיק מתחת ל-Header */
}




.home-title{
    font-family: "karantina";
    font-size: 3.5em;
    font-weight: 400;
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    width: 90vw;
    top: 13%;

}

